.subscribe-link {
    font-family: $ff-helvetica-neue;
    font-weight: $fw-medium;
    font-size: 11px;
    color: $color-black;
    text-decoration: none;
    text-align: center;

    &--footer {
        margin-bottom: 24px;
    }

    span {
        display: block;
        text-transform: uppercase;
        font-size: 8px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        line-height: 1.5;
        margin-top: 8px;
        @include linkhover($color-black);
    }

    &:hover {
        span:after {
            transform: scaleX(1);
            transform-origin: bottom left;
        }
    }
}



