.burger-menu {

    $this: &;

    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;

    &__list {
        @include reset-list();
        list-style: none;
        counter-reset: custom-counter;

        @include desktop {
            border-top: 2px solid $color-grey;
        }
    }

    &__item {
        position: relative;
        font-size: 30px;
        padding: 12px 20px 20px;
        border-bottom: 2px solid $color-grey;
        text-align: center;

        @include desktop {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px 20px 12px;
        }

        counter-increment: custom-counter;

        &::before {
            content: '0'counter(custom-counter) '. ';
            color: $color-red;
            font-family: $ff-financier-display;
            font-weight: $fw-light;
            font-size: 20px;
            font-style: italic;
        }
    }

    &__link {
        display: block;
        position: relative;
        overflow: hidden;
        outline: none;
        text-decoration: none;
        color: $color-black;

        @include desktop {
            padding-left: 20px;
        }

        &:hover {
            #{$this}__title {
                animation: MoveUpInitial 0.2s forwards, MoveUpEnd 0.2s forwards 0.2s;
            }

            #{$this}__intro {
                opacity: 1;
                max-height: 100px;
            }
        }

    }

    &__title {
        display: block;
        position: relative;
        z-index: 1;
        overflow: hidden;
        font-size: 35px;
        line-height: 1;
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        text-transform: uppercase;

        @include desktop {
            font-size: 60px;
        }

        @include desktop-wide {
            font-size: 90px;
        }
    }


    &__intro {
        display: none;

        @include desktop {
            display: block;
            opacity: 0;
            max-height: 0;
            transition: opacity 0.4s, max-height 0.4s;
            font-family: $ff-financier-display;
            font-weight: $fw-light;
            font-style: italic;
            font-size: 22px;
            line-height: 1.45;
        }
    }

    @keyframes MoveUpInitial {
        from {
            transform: translate3d(0,0,0);
        }
        to {
            transform: translate3d(0,100%,0);
        }
    }

    @keyframes MoveUpEnd {
        from {
            transform: translate3d(0,-100%,0);
        }
        to {
            transform: translate3d(0,0,0);
        }
    }

    &__logo-subscribe {
        display: flex;
        justify-content: center;
        gap: 85px;
        margin-top: 53px;

        @include desktop {
            display: none;
        }
    }

    &__subscribe {
        display: block;

        @include desktop {
            display: none;
        }
    }

    &__kf-link {
        display: block;

        @include desktop {
            display: none;
        }
    }

    &__content-wrapper {
        text-align: center;
    }

    &__content {
        @include reset-list();
        margin: 18px auto 30px;
        max-width: 1312px;
        color: $color-black;
        display: flex;
        justify-content: center;

        @include desktop {
            justify-content: space-evenly;
            margin: 54px auto;
        }
    }

    &__content-item {
        text-align: center;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        font-size: 10px;
        line-height: 1.4;
        letter-spacing: 2;
        max-width: 305px;

        &:not(:first-child) {
            display: none;

            @include desktop {
                display: block;
            }
        }
    }

}
