.modal {
    $this: &;

    position: fixed;
    z-index: $layer-modal;

    @include desktop-only {
        --container-width: 100vw;
    }

    &__overlay {
        position: absolute;
        z-index: -1;
        background-color: var(--color-tranparent-black);
    }

    &__close {
        position: absolute;
        top: 0;
        right: 0;
        background-color: var(--color-white);
        color: var(--color-black);

        @include hover-focus {
            .icon {
                transform: scale(0.87);
            }
        }

    }

    &__content {
        position: relative;
        overflow-y: auto;
        min-height: var(--viewport-height);
        max-height: var(--viewport-height);
        color: var(--color-white);
        -webkit-overflow-scrolling: touch;
    }

    &__content.is-scrollable + &__close {
        right: var(--scrollbar);
    }

    &,
    &__overlay {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &--burger {
        width: auto;
        top: var(--top-menu-height);
        right: 0;
        background-color: $color-beige;

        @include desktop {
            top: 0;
            right: var(--sidebar-width);
        }

        #{$this}__overlay {
            background-color: transparent;
        }

        #{$this}__content {
            min-height: calc( var(--viewport-height) - var(--top-menu-height));
            max-height: calc( var(--viewport-height) - var(--top-menu-height));
            @include desktop {
                display: flex;
                flex-direction: column;
                align-items: stretch;
                min-height: var(--viewport-height);
                max-height: var(--viewport-height);
            }
        }
    }
}
