.sidebar {
    display: flex;
    justify-content: stretch;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    border-bottom: 1px solid var(--color-grey);
    height: var(--top-menu-height);
    background-color: $color-white;
    z-index: 1;

    padding: 15px 25px;

    @include desktop {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        width: var(--sidebar-width);
        height: var(--viewport-height);
        border-left: 1px solid var(--color-grey);
        border-bottom: 0;
        padding: 34px 0 34px;
    }

    .is-menu-open & {
        background-color: $color-beige;
    }

    .theme-beige & {
        background-color: $color-beige;
    }

    &__the-view-title {
        font-size: 30px;
        line-height: 1;
        flex: 1;
        display: flex;
        justify-content: center;

        @include desktop {
            display: none;
        }
    }

    &__footer {
        display: none;

        @include desktop {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }
}
